@import '../../sass-modules/breakpoints';
@import '../../sass-modules/colors';

.footer {
  text-align: center;
  padding-bottom: 2rem;

  @media screen and (min-width: $break-footer) {
    text-align: left;
  }
}

/* logo --- */
.footer__logo {
  margin-right: 1rem!important;
}

/* ===============================
Facebook
================================== */
.facebook {
  width: 2.2rem;
  vertical-align: middle;
}

/* ===============================
Copyright
================================== */
.copyright {
  color: $light-grey;
}

.footer__copyright {
  @media screen and (min-width: $break-footer) {
    float: right;
  }
}